.site-header .main-header .search{
  position: relative;
  top: 30px;
}
nav.main-navigation .fix{
    position:fixed;
    top:0;
    left:0;
    right:0;
    margin:0;
}
.site-header .main-header .logo a img{
  width: 18%;
}
.site-header .main-header span.header{
  font-size: 40px;
  text-transform: uppercase;
  color: #fff;
  vertical-align: middle;
  margin-left: 20px;
  font-family: 'Open Sans', sans-serif;
}
.blog-single-item img {
    width: 100%;
}
section.page-heading .container ol.breadcrumb{
  background-color: #fff;
}
.breadcrumb > li + li:before {
    content: ">";
    padding: 0 5px;
    color: #cccccc;
}
footer.footer .address p{
  font-size: 18px;
  padding-top: 10px;
}
@media (max-width: 48em){
  .site-header .main-header .logo {
    font-size: 28px;
    position: relative;
    top: -25px;
  }
  .site-header .main-header .logo a img{
    position: relative;
    top: 10px;
  }
  .site-header .main-header span.header{
    font-size: 14px;
    margin: 0;
  }
  .site-header .main-header .search{
    position: relative;
    top: 80px;
  }
  a.side-menu-button i {
      position: relative;
      top: -95px;
  }

  .site-header .main-header .search .inner-addon { 
    width: 150px !important;
    position: absolute !important;
    left: 27%;
    top: 10%;
  }
  .site-header .main-header .search .inner-addon input {
    position: absolute;
    width: 150px;
  }
  .services {
    float: none;
  }
  .services .right-text {
    width: 100%;
    margin: 20px 0;
    padding: 0;
  }
  .services .left-image img {
    width: 200%;
    max-height: 100%;
}
.call-to-action-2 {
  float: none;
}
.call-to-action-2 .right-image {
    width: 100%;
    height: 35%;
}
.call-to-action-2 .left-text {
  width: 100%;
  padding: 0;
  margin-top: 225px;
}
.owl-navigation .go-to {
  font-size: 10px;
  letter-spacing: 0px;
  width: 125px;
  height: 60px;
}


@media (min-width: 672px){
  .site-header .main-header .logo {
    font-size: 40px;
    position: relative;
  }
  .site-header .main-header .search .inner-addon { 
    width: 250px !important;
    position: absolute !important;
    left: 35%;
    top: 10%;
  }
  .site-header .main-header .search .inner-addon input {
    position: absolute;
    width: 250px;
  }
  .services {
    float: none;
  }
  .services .right-text {
    width: 100%;
    margin: 20px 0;
    padding: 0;
  }
  .services .left-image img {
    width: 200%;
    max-height: 100%;
  }
  .call-to-action-2 {
    float: none;
  }
  .call-to-action-2 .right-image {
    width: 100%;
    height: 60%;
  }
  .call-to-action-2 .left-text {
    width: 100%;
    padding: 0;
    margin-top: 425px;
  }
}

@media (min-width: 768px){
  .site-header .main-header .search .inner-addon { 
    position: absolute !important;
    left: -435px;
  }
  .call-to-action-2 .right-image {
    width: 100%;
    height: 60%;
  }
  .call-to-action-2 .left-text {
    width: 100%;
    padding: 0;
    margin-top: 425px;
  }
}

@media (min-width: 1024px){
  .site-header .main-header .search {
      position: relative;
      top: -20px;
  }
  .site-header .main-header span.header{
    font-size: 36px;
    margin: 0;
  }
}